<template>
  <div id="room">
    <el-row :gutter="15">
      <el-col :span="3">
        <img :src="host.avatar" :alt="host.nickname" id="host-avatar">
      </el-col>
      <el-col :span="16">
        <h2 id="room-title">{{room.title}}</h2>
        <span id="host-nickname">{{host.nickname}}</span> <router-link :to="`/category/${category.name}`"><el-button type="text">{{category.title}}</el-button></router-link>
      </el-col>
    </el-row>
    <div class="margin-10"></div>
    <el-row :gutter="10">
      <el-col :span="18">
        <video ref="player" id="stream-player" class="video-js vjs-default-skin"
          controls preload="auto"
          data-setup='{"example_option":true}'>
          <source :src="rtmp" type="rtmp/mp4">
          <source :src="hls" type="application/x-mpegURL">
        </video>
      </el-col>
      <el-col :span="6">
        <div id="comments-wrapper" ref="commentsWrapper">
          <ul id="comments">
            <li v-for="comment in comments">
              <span class="comment-nickname">{{comment.user.nickname}}</span>: {{comment.content}}
            </li>
          </ul>
        </div>
        <div id="comment-sender">
          <span id="comment-label"></span>
          <textarea id="content-textarea"></textarea>
          <el-button type="primary" icon="check" size="small"></el-button>
          <div id="comment-sender-mask">
            
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import 'video.js/dist/video-js.min.css'
  import videojs from 'video.js'
  import 'videojs-contrib-hls'

  videojs.options.flash.swf = '/public/video-js.swf'

  export default {
    data() {
      return {
        room: {
          streamKey: 'e04b1b4f56cf4848e9a9896d7818318c557bed4f00700d9fa3a08ce38240b8580a6bdda541a42abc0b0dfec9775c0b20b852a2ab2d09813bc6556aa48a8a84ce',
          title: 'DEMO',
          active: false
        },
        host: {
          username: 'demo',
          nickname: 'demo',
          avatar: 'http://localhost:7001/public/images/default_avatar.png'
        },
        category: {
          name: 'overwatch',
          title: ''
        },

        comments: [
          {
            user: { nickname: 'DEMO' },
            content: 'TEST Comment'
          },
          {
            user: { nickname: 'DEMO' },
            content: 'TEST Comment'
          },
          {
            user: { nickname: 'DEMO' },
            content: 'TEST Comment'
          }
        ]
      }
    },

    mounted() {
      const width = this.$refs.player.offsetWidth
      const height = width / 16 * 9
      this.$refs.player.style.height = height + 'px'
      this.$refs.commentsWrapper.style.height = height - 100 + 'px'

      const options = {
        hls: {
          withCredentials: true
        }
      }

      const player = videojs(this.$refs.player, {
        html5: options,
        flash: options
      })

      if (this.room.active) {
        player.play()
      }
    },

    computed: {
      rtmp() {
        return `rtmp://${location.hostname}/live/${this.room.streamKey}`
      },
      hls() {
        return `http://${location.hostname}:8081/live/${this.room.streamKey}.m3u8`
      }
    }
  }
</script>

<style scoped>
  #host-avatar {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 40px;
    border: 3px solid #20A0FF;
    float: right;
  }

  #room-title {
    color: #1F2D3D;
    margin: 0.7rem 0;
  }

  #host-nickname {
    color: #99A9BF;
  }

  .margin-20 {
    display: block;
    margin-bottom: 20px;
  }
  .margin-10 {
    display: block;
    margin-bottom: 10px;
  }

  #stream-player {
    width: 100%;
  }

  #comments {
    list-style: none;
    padding: 0;
    margin: 10px;
  }

  #comments li {
    margin-bottom: 5px;
    color: #1F2D3D;
    font-size: 14px;
    overflow: hidden;
  }

  .comment-nickname {
    color: #20A0FF;
  }

  #comments-wrapper {
    position: relative;
    width: 100%;
    border: 1px solid #ECECEC;
  }

  #comment-sender {
    width: 100%;
    height: 100px;
    border: 1px solid #ECECEC;
    margin-top: -1px;
    position: relative;
  }

  #comment-sender #content-textarea {
    width: 100%;
    height: calc(100% - 24px);
    border: none;
    resize: none;
    outline: none;
    padding: 3px 10px;
  }

  #comment-sender .el-button {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  #comment-label {
    font-size: 14px;
    padding: 8px;
    line-height: 24px;
    color: #99A9BF;
  }

  #comment-sender-mask {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #FFF;
    opacity: 0.9;

    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
</style>


