<template>
  <el-menu id="main-sidebar" mode="vertical" theme="dark" default-active="1">
    <img id="logo" src="/public/images/light-logo.png" />
    <router-link to="/"><el-menu-item index="1">
      <i class="el-icon-star-on"></i>
    </el-menu-item></router-link>
    <el-submenu index="2" default-active="2-1">
      <template slot="title"><i class="el-icon-menu"></i></template>
      <el-menu-item index="2-1"></el-menu-item>
      <el-menu-item-group title="">
        <el-menu-item
          v-for="(category, index) in staredCategories"
          :index="`2-${(index + 2)}`"
        >{{category.title}}</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
    <el-menu-item index="3">
      <i class="el-icon-circle-check"></i>
    </el-menu-item>

    <div v-if="user" id="user-info">
      <img :src="user.avatar">
      <span>{{user.nickname}}</span>
    </div>
    <div v-else id="user-info">
      <img src="/public/images/default_avatar.png">
      <span>/</span>
    </div>
  </el-menu>
</template>

<script>
  export default {
    props: [ 'staredCategories', 'user' ]
  }
</script>

<style scoped>
  #main-sidebar {
    width: 200px;
    height: 100vh;
    position: fixed;
  }

  #logo {
    width: 100%;
  }

  #user-info {
    width: 100%;
    padding: 10px 18px;

    position: absolute;
    bottom: 0;

    cursor: pointer;
  }

  #user-info img {
    width: 50px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 25px;
  }

  #user-info span {
    color: #c0ccda;
    font-size: 14px;
    margin-left: 10px;
  }

  .el-menu a {
    text-decoration: none;
    color: inherit;
  }
</style>


